.btn {
    background-color: aquamarine;
    padding: 13px 23px ;
    color: $white;
    text-transform: uppercase;
    font-size: .7rem;
    transition: all $transition;
    text-shadow : 0 0 2px #fff;
    border-radius: 8px 0 8px 0;
    border: none;
    &:hover {
        background-color: $black-txt;
        color: $white;
    border-radius: 8px;

    }
    @media screen and (max-width: 768px) {
        padding: 1.1em 2em;
    }
}

.btn2 {
    border-radius: 0;
}

